.file-div {
  width: 250px;
  height: 100%;
  padding: 20px;
  background-color: lightgrey;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.file-upload-input {
  display: none;
}

.upload-btn {
  background-color: rgb(239, 239, 239);
  box-shadow: 0px 4px 4px rgb(0, 0, 0, 0.25);
  text-align: center;
  line-height: 46px;
  color: black;
}

.upload-btn:active {
  background-color: rgb(155, 155, 155);
}

.upload-btn:hover {
  cursor: pointer;
}

.upload-btn,
.add-db-btn,
.delete-db-btn {
  width: 100px;
  height: 46px;
}

.file-footer {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.file-body {
  margin-top: 20px;
  height: 440px;
  width: 100%;
  overflow: hidden;
  overflow-y: scroll;
}

.chosen-file {
  border: 4px solid rgba(0, 194, 0, 0.787) !important;
}

.file-name {
  margin-top: 10px;
}

.file-name-label {
  font-size: 18px;
}

.file-name-input {
  width: 100%;
  height: 46px;
  font-size: 18px;
  background-color: rgb(239, 239, 239);
  border: none;
  outline: none;
  box-shadow: 0px 4px 4px rgb(0, 0, 0, 0.25);
  padding: 10px;
}

.file-updated {
  width: 100%;
  font-size: 18px;
  margin-top: 10px;
}

.file-updated-date {
  font-weight: bold;
}

.file-group {
  width: 100%;
  height: 46px;
  border: none;
  outline: none;
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 10px;
  background-color: rgb(239, 239, 239);
}
