.disabled {
  display: none;
}

.file-box {
  width: 100%;
  padding: 5px 10px 10px 10px;
  font-size: 18px;
  margin-top: 2px;
  background-color: rgb(239, 239, 239) !important;
}

.file-box:hover {
  cursor: pointer;
}

.file-icon {
  width: 40px;
  height: 40px;
}

.file-head {
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.file-bibtex-name {
  margin-top: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: bold;
  color: rgb(117, 31, 255);
}

.file-info {
  display: flex;
  align-items: center;
}

.file-info-name {
  margin-left: 5px;
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.file-date {
  font-weight: bold;
  font-size: 16px;
}
