.group-list {
  width: 400px;
  height: 100%;
  padding: 20px;
  background-color: lightgrey;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.clicked {
  background-color: rgb(160, 160, 160);
}

.group-body {
  margin-top: 20px;
  width: 100%;
  min-height: 440px;
  overflow-x: hidden;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.group-footer {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.input-group-label {
  font-size: 18px;
}

.input-group-name {
  width: 100% !important;
  font-size: 18px;
}

.input-group-name:focus {
  border: 2px solid orange !important;
  box-shadow: 0px 4px 4px rgba(255, 102, 0, 0.25);
}

.group-create-btn {
  margin-top: 15px;
  background-color: rgb(0, 190, 0);
  color: white;
}

.group-create-btn:active {
  background-color: rgb(0, 114, 0) !important;
}

.disabled {
  display: none;
}
