.Group {
    display: flex;
    justify-content: space-between;
    height: 700px;
}

.Group button {
    height: 46px;
    font-size: 18px;
    border: none;
    outline: none;
    box-shadow: 0px 4px 4px rgb(0, 0, 0, .25);
    padding-left: 10px;
    padding-right: 10px;
    width: 100px;
}

.Group button:active {
    background-color: rgb(155, 155, 155);
}

.Group input {
    border: none;
    outline: none;
    box-shadow: 0px 4px 4px rgb(0, 0, 0, .25);
    height: 46px;
    width: 150px;
    padding-left: 5px;
    padding-right: 5px;
}

.Group input:focus {
    border: none;
    outline: none;
}