.group-box {
    display: flex;
    flex-direction: column;
    font-size: 18px;
    width: 300px;
    background-color: rgb(239, 239, 239);
    margin-top: 2px;
    padding: 5px 10px 10px 10px;
    height: 170px;
}

.box-header {
    display: flex;
    justify-content: space-between;
}

.box-name {
    font-weight: bold;
    font-size: 24px;
    color: rgb(0, 119, 255);
}

.box-image {
    width: 30px;
    height: 30px;
}

.box-owner,
.box-members {
    display: flex;
}

.box-owner-name,
.box-members-name {
    margin-left: 10px;
    font-weight: bold;
}

.box-owner-label,
.box-members-label {
    font-style: italic;
}

.box-click-btn {
    margin-top: 10px;
    color: white;
    width: 170px !important;
}

.box-dang {
    background-color: rgb(255, 37, 37);
}

.box-safe {
    background-color: rgb(0, 182, 0);
}

.box-dang:active {
    background-color: crimson !important;
}

.box-safe:active {
    background-color: rgb(0, 128, 0) !important;
}

.selected-group {
    border: 5px solid rgb(255, 102, 0);
}