.entry-div {
  width: 400px;
  height: 100%;
  padding: 20px;
  background-color: lightgrey;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.entry-inactive {
  width: 100%;
  text-align: center;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 36px;
}

.entry-search {
  margin-top: 25px;
  display: flex;
  width: 100%;
  flex-direction: column;
}

.entry-search label {
  font-size: 18px;
  margin-bottom: 2px;
  font-weight: bold;
}

.entry-search-input {
  height: 46px;
  font-size: 18px;
  background-color: rgb(239, 239, 239);
  border: none;
  outline: none;
  box-shadow: 0px 4px 4px rgb(0, 0, 0, 0.25);
  padding: 10px;
}

.entry-search-input:focus {
  box-shadow: 0px 4px 4px rgba(255, 94, 0, 0.25);
  border: 2px solid rgb(255, 127, 53);
}

.entry-body {
  margin-top: 20px;
  width: 100%;
  height: 430px;
  overflow: hidden;
  overflow-y: scroll;
}

.add-entry-btn,
.download-btn {
  height: 46px;
}

.download-btn {
  margin-top: 20px;
  width: 100px;
  background-color: rgb(239, 239, 239);
  text-decoration: none;
  color: black;
  text-align: center;
  line-height: 46px;
  box-shadow: 0px 4px 4px rgb(0, 0, 0, 0.25);
}

.disabled {
  display: none;
}
