.edit-window-layout {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.52);
}

.edit-window-main {
  position: absolute;
  width: 350px;
  padding: 15px 20px 15px 20px;
  top: 450px;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #f2f2f2;
}

.edit-window-body {
  max-height: 500px;
  overflow-x: hidden;
  overflow-y: scroll;
  padding-right: 20px;
}

.disabled {
  display: none;
}

.edit-window-head {
  font-size: 32px;
  font-weight: bold;
}

.edit-info {
  margin-top: 15px;
}

.info-label {
  font-size: 18px;
  font-style: italic;
  display: flex;
  justify-content: space-between;
  font-weight: bold;
}

.info-label-tag {
  margin-left: 4px;
}

.required-div {
  font-style: italic;
  color: rgb(0, 132, 255);
}

.info-input {
  width: 100%;
  resize: vertical;
  min-height: 46px;
  max-height: 300px;
  height: 46px;
  font-size: 18px;
  border: none;
  outline: none;
  padding: 10px;
  border-radius: 6px 6px 6px 6px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.info-input:focus {
  border: 1px solid rgb(255, 99, 38);
  box-shadow: 0px 6px 6px rgba(255, 72, 0, 0.25);
}

.edit-window-footer {
  margin-top: 10px;
  display: flex;
  justify-content: center;
}

.edit-window-footer button {
  font-size: 18px;
  height: 46px;
  width: 150px;
  color: white;
  font-weight: bold;
}

.edit-cancel-btn {
  background-color: rgba(255, 42, 42, 0.815);
}

.edit-cancel-btn:active {
  background-color: rgb(177, 21, 52) !important;
}

.edit-save-btn {
  margin-left: 30px;
  background-color: rgb(0, 197, 43);
}

.edit-save-btn:active {
  background-color: rgb(0, 141, 31) !important;
}
